"use client";
import { useState, useEffect } from "react";
import { investments } from "@/lib/data";

export default function Dashboard() {
  const [storedInvestments, setStoredInvestments] = useState([]);

  useEffect(() => {
    if (typeof window !== "undefined") {
      const savedInvestments = JSON.parse(localStorage.getItem("swipedBusinesses") || "[]");
      setStoredInvestments(savedInvestments);
    }
  }, []);

  return (
    <div className="p-6 bg-gradient-to-b from-gray-900 to-blue-900 min-h-screen text-gray-200 font-sans">
      <h1 className="text-4xl font-bold mb-6 text-center text-gray-100 drop-shadow-lg tracking-wide">My Current Investments</h1>
      
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {investments.map((inv) => (
          <div key={inv.id} className="border border-gray-700 rounded-lg p-6 shadow-xl bg-gray-800 transition-transform transform hover:scale-105 hover:shadow-2xl">
            <h2 className="text-2xl font-semibold text-white mb-2">{inv.name}</h2>
            <p className="text-gray-400 text-lg font-medium">{inv.industry}</p>
            <p className="mt-2 text-gray-300 font-medium">Quarterly Revenue: <span className="text-blue-400">{inv.quarterlyRevenue}</span></p>
            <p className="text-gray-300 font-medium">Employees: <span className="text-blue-400">{inv.employees}</span></p>
            <p className="text-green-400 font-bold text-lg mt-3">Risk: {inv.riskAssessment}</p>
          </div>
        ))}
      </div>
    </div>
  );
}
